@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;700;800&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}

html{height:100%}body{position:relative;height:100%;min-width:320px;font-family:Inter, sans-serif;font-size:18px;line-height:1.33;background:#1d0f2a;color:#ffffff}#root{height:100%}button,input,textarea{border:none;margin:0;padding:0;background:transparent;color:inherit;font:inherit;line-height:normal}input,textarea{outline:none}button{width:auto;cursor:pointer}

.app{height:100%}

.quiz,.quizQuestion,.quizCheckout{display:-webkit-flex;display:flex;min-height:100%}.quizQuestion{background:#136bd9;-webkit-align-items:center;align-items:center}.quizCheckout{background:linear-gradient(180deg, #45204b, #342653 73%)}.container{padding:20px 24px;width:100%;max-width:550px;margin:0 auto}

.single-select-question__head{margin-bottom:20px}@media only screen and (min-width: 480px){.single-select-question__head{text-align:center}}.single-select-question__title{font-weight:800;font-size:24px;line-height:28px}.single-select-question__title b{color:#ffe000}.single-select-question__subtitle{margin-top:6px}.single-select-question__subtitle b{font-weight:bold;color:#ffe000}.single-select-question__answer{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;margin:0 auto 8px;padding:16px;background:#ffe000;width:100%;min-height:90px;max-width:380px;border-radius:8px;text-align:left;box-shadow:0 0 6px 0 rgba(0,0,0,0.2)}.single-select-question__answer:last-child{margin-bottom:0}.single-select-question__icon{font-size:44px;margin-right:16px}.single-select-question__answer-title{font-weight:600;color:#1d0f2a;font-size:20px}

.input-question__head{margin-bottom:50px}@media only screen and (min-width: 480px){.input-question__head{text-align:center}}.input-question__title{font-weight:800;font-size:24px;line-height:28px}.input-question__title b{color:#ffe000}.input-question__subtitle{margin-top:6px}.input-question__subtitle b{font-weight:bold;color:#ffe000}.input-question__input{display:block;border-style:none none solid;border-width:0 0 1px;border-color:transparent transparent #4288e0;background-color:#136bd9;height:24px;margin-bottom:45px;font-size:18px;line-height:24px;width:100%;padding:8px 12px;text-align:center}.input-question__btn{display:block;width:100%;max-width:360px;margin:auto;padding:17px;border-radius:12px;background-color:#ffffff;color:#136bd9;font-size:16px;line-height:1;font-weight:700;text-align:center}.input-question__btn:disabled{opacity:0.5}

.progress-loader{width:100%;text-align:center}

.checkoutIcon{font-size:60px;text-align:center;margin:0 auto 16px}.checkoutButton{display:block;width:100%;max-width:360px;margin:0 auto 24px;padding-top:15px;padding-bottom:13px;border-radius:99px;background-color:#0071ff;text-align:center;letter-spacing:1px;font-weight:bold;font-size:16px;line-height:24px}.checkoutButton b{font-weight:800}.checkoutText{text-align:center;font-size:16px;line-height:20px;font-weight:500}.checkoutText strong{font-size:22px;line-height:27px;font-weight:700}.checkoutText b{font-size:18px;line-height:1.2;font-weight:700}.checkoutText em{font-style:italic}.checkoutCards{padding-top:24px;margin-bottom:28px}.checkoutCardsTitle{font-size:22px;line-height:1.2;margin-bottom:24px;font-weight:700;text-align:center}.checkoutCard,.checkoutCardSelected{position:relative;display:grid;grid-template-columns:1fr 78px;min-height:78px;cursor:pointer}.checkoutCard:not(:last-child),.checkoutCardSelected:not(:last-child){margin-bottom:22px}.checkoutCardLabel{position:absolute;left:16px;bottom:calc(100% - 12px);padding:4px 8px;background:#827791;border-radius:6px;font-weight:700;font-size:10px;line-height:1.6;letter-spacing:0.01em;color:#ffffff}.checkoutCardSelected .checkoutCardLabel{background-image:linear-gradient(180deg, #e20dbe, #e4004f)}.checkoutCardPeriod{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:10px;background:#4c3b62;border-radius:8px 0 0 8px;border-style:solid;border-width:1px;border-color:#827791;color:#827791;font-weight:800;font-size:16px;letter-spacing:0.5px}.checkoutCardSelected .checkoutCardPeriod{color:#ffffff;background:#292b59;border-color:#0071ff}.checkoutCardPrice{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:10px;background:#4c3b62;border-radius:0 8px 8px 0;border-style:solid;border-width:1px 1px 1px 0;border-color:#827791;color:#827791;font-weight:700;font-size:16px}.checkoutCardSelected .checkoutCardPrice{color:#ffffff;background:#0071ff;border-color:#0071ff}.moneyBack{position:relative;border-radius:14px;padding:12px;background-color:#292b59;background-image:linear-gradient(165deg, rgba(94,60,180,0.5), rgba(94,60,180,0.3));margin-bottom:40px}.moneyBackContent{border:2px solid #45e2ad;border-radius:12px;padding:24px 16px 40px}.moneyBackTitle{margin-bottom:12px;font-weight:700;font-size:24px;line-height:1.2;text-align:center}.moneyBackText{font-size:16px;line-height:20px;font-weight:500}.moneyBackStamp{position:absolute;right:34px;bottom:-40px;fill:#45e2ad}

.thank-you__title{font-weight:800;font-size:24px;line-height:28px;margin-bottom:20px}.thank-you__title b{color:#ffe000}.thank-you__subtitle{margin-bottom:6px}.thank-you__subtitle b{font-weight:bold;color:#ffe000}

